.catalog {
  padding: 30px 33px 20px 20px;
  background-color: #f2f2f2;
  position: relative;
  margin-bottom: 20px;

  &::after {
    position: absolute;
    content: "";
    width: 50px;
    height: 50px;
    top: 25px;
    left: 22px;
    border-radius: 50%;
    background-color: $basic-background;
    z-index: 1;
  }

  @media (min-width: $tablet-width) {
    padding: 77px 314px 64px 53px;
    background-position: 25% 83%;
    margin-bottom: 30px;

    &::after {
      top: 92px;
      left: 446px;
      width: 200px;
      height: 200px;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 77px 51px 63px 52px;
    background-position: 30% 83%;
    min-width: 570px;
    margin-right: 80px;
    margin-bottom: 0;

    &::after {
      top: 47px;
      left: 52px;
      width: 100px;
      height: 100px;
    }
  }
}

.catalog__icon-loss {
  position: absolute;
  top: 21px;
  left: 30px;
  z-index: 2;

  @media (min-width: $tablet-width) {
    width: 140px;
    height: 194px;
    top: 80px;
    left: auto;
    right: 91px;
  }

  @media (min-width: $desktop-width) {
    width: 70px;
    height: 97px;
    top: 41px;
    left: 67px;
  }
}

.catalog__icon-weight {
  position: absolute;
  top: 37px;
  left: 13px;
  z-index: 2;

  @media (min-width: $tablet-width) {
    width: 268px;
    height: 112px;
    top: 36%;
    left: auto;
    right: 28px;
  }

  @media (min-width: $desktop-width) {
    width: 134px;
    height: 56px;
    top: 67px;
    left: 33px;
  }
}

.catalog__title {
  @include catalog-title;
  margin: 0;
  margin-left: 71px;
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    @include heading;
    line-height: 36px;
    margin: 0;
    margin-bottom: 25px;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
    text-align: center;
    margin-bottom: 73px;
    margin-left: 20px;
  }
}

.catalog__title--weight {
  @media (min-width: $desktop-width) {
    margin-left: 61px;
  }
}

.catalog__intro {
  font-family: "Arial", sans-serif;
  margin: 0;
  margin-bottom: 42px;
  position: relative;


  @media (max-width: $mobile-width) {
    &::after {
      content: "";
      position: absolute;
      width: 240px;
      height: 1px;
      bottom: -23px;
      left: 0;
      background-color: #d9d9d9;
    }
  }

  @media (min-width: $tablet-width) {
    @include cover-text;
    line-height: 24px;
    color: #000000;
    margin-bottom: 54px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 32px;
  }
}

.catalog__service {
  @include text;
  display: block;
  text-decoration: none;

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    @include tablet-text;
  }
}

.catalog__service-arrow {
  margin-left: 17px;
  transform: scaleX(1);
  transform-origin: center left;
  transition: transform 0.5s;

  @media (min-width: $tablet-width) {
    margin-left: 25px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 28px;
  }
}

.catalog__service:hover .catalog__service-arrow {
  transform: scaleX(1.4);
}

.catalog__service:active .catalog__service-arrow {
  transform: scaleX(1.4);
  opacity: 0.3;
}
